<template>
    <div id="td_detail">
		<div style="overflow: hidden;" class="taskDetail">
			<div class='wrapper'>
				<div class='detail'>
					<p class="name">{{detailInfo.requireName}}</p>
					<p class="status1">{{detailInfo.status}}</p>
				</div>
				<ul>
					<li>
						<span>需求名称:</span>
						<i>{{detailInfo.requireName}}</i>
					</li>
					<li>
						<span>发&nbsp;起&nbsp;人:</span>
						<i>{{detailInfo.crtName}}</i>
					</li>
					<li>
						<span>发起时间:</span>
						<i>{{detailInfo.crtTime}}</i>
					</li>
					<li>
						<span>优&nbsp;先&nbsp;级:</span>
						<i>{{detailInfo.level}}</i>
					</li>
					<li v-if="detailInfo.status=='进行中'">
						<span style="color:#E64340;" >预计交付日：</span>
						<input type="date"/>
					</li>
				</ul>
				<div class='process'>
					<div class='agree_status'>
						<div v-for="tab in infoList" class='allInfo'>
							<div class='info' >
								<i class='iconfont icon-shenfenzheng'></i>
								<p>
									<span>{{tab.userName}}</span>
									<span>{{tab.nodeName}}</span>
									<span>{{tab.endTime}}</span>
								</p>
								<i class=" weui-icon weui-icon-success" v-if="tab.status"></i>
								<i class=" weui-icon weui-icon-waiting" v-if="!tab.status" style="top:15px;"></i>
								<em class='taskStatus'>{{tab.status}}</em>
								
							</div>
							<div class='iconfont icon-xia arrow' ></div>
						</div>
						
					</div>
				</div>
			</div>
		</div>        
		<div class="btn">
			<span @click="agree()">同意</span>
			<span @click="reject()">拒绝</span>
		</div>
		<div class="js_dialog" id="iosDialog1" style="opacity: 1;" v-if="showDialog">
            <div class="weui-mask"></div>
            <div class="weui-dialog">
                <div class="weui-dialog__bd">{{dialogInfo}}</div>
                <div class="weui-dialog__ft">
                    <a href="javascript:;" class="weui-dialog__btn weui-dialog__btn_default" @click="close()">确认</a>
                    <a href="javascript:;" class="weui-dialog__btn weui-dialog__btn_primary" @click="close()">取消</a>
                </div>
            </div>
        </div>
	</div>
</template>

<script>
import { getListTask} from '../../service/service.js'
export default{
	data(){
		return{
			infoList:[],
			detailInfo:{},
			showDialog:false,
			dialogInfo:"确定同意该请求吗？"
		}
	},
	methods:{
		agree(){
			this.showDialog = true;
		},
		reject(){
			this.dialogInfo = "确定拒绝该请求吗";
			this.showDialog = true;
		},
		close(){
			this.showDialog = false;
		}

	},
	created(){
		var url = location.href;
		var arr = url.split("?");
		var arr1 = arr[1].split("&")
		let code = arr1[0].split("=")[1];
		console.log(code);
		this.detailInfo = this.$route.query.info;
		let token  = localStorage.getItem("token");
		getListTask(this.detailInfo.businessKey,token).then((result)=>{
			this.infoList = result;
			
		})
	},
	mounted(){
		 let detailScroll = new IScroll('.taskDetail', {
					probeType: 3
			});
		
			
	}
	
}
    
</script>

<style>
#td_detail{
	width:100%;
	position:absolute;
	left:0;
	top:0;
	bottom:0;
	z-index:100;	
	background: #fff;
}
.detail{
	width:100%;
	border-bottom: 1px solid #ccc;
	padding: 15px;
	font-size:20px;
	color:#000;
	font-weight: 700;
	text-align: center;
	position: relative;
}
.detail .name{
	display:inline-block;
	width:200px;
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
}
.detail .status1{
	width:50px;
	line-height: 20px;
	padding: 2px 0;
	text-align: center;
	color:#fff ;
	background:#10AEFF ;
	position:absolute;
	right: 15px;
	top:18px;
	font-size:12px;
}
#td_detail ul{
	border-bottom: 1px solid #ccc;
	padding: 15px;
}
#td_detail ul li{
	line-height: 30px;
	display: flex;
	
}
ul li span{
	color:lightslategrey;
	
}
ul li i{
	color:#000;
	flex:1;
	margin-left:5px;
}
.btn{
	width:100%;
	position: fixed;
	left:0;
	bottom:0; 
	text-align: center;
	display: flex;
	height:40px;
	line-height: 40px;
	color:#fff;
	font-size:16px;
}
.btn span{
	flex:1;
}
.btn span:first-child{
	background-color: #09bb07;
}
.btn span:last-child{
	background-color: #E64340;
}
.process{
	width:100%;
	overflow: hidden;
	margin-bottom:30px;
}
.process .agree_status{	
	margin:15px;
}
.agree_status .info{
	border:1px solid #ccc;
	padding: 5px 10px;
	position: relative;
	vertical-align: middle;
}
.allInfo:first-child{
	margin-top:10px;
}
.allInfo:last-child .arrow{
	display: none;
}
.info .iconfont{
	position: absolute;
	top:20px;
}
.info p{
	display: inline-block;
	margin-left:30px;
}
.info p span{
	display: block;
	margin-bottom:5px;
}
.taskStatus{
	position: absolute;
	right:26px;
	top:7px;
}
.weui-icon{
	position:absolute;
	right:25px;
	top:35px;
}
.arrow{
	text-align: center;
}
</style>


