<template>
	<div>
		<div id="td" class="page">
			<div class='wrapper'>
				<div class="weui-search-bar" id="searchBar">
					<form class="weui-search-bar__form">
						<div class="weui-search-bar__box">
							<i class="weui-icon-search"></i>
							<input type="search" class="weui-search-bar__input" id="searchInput" placeholder="搜索" required="">
							<a href="javascript:" class="weui-icon-clear" id="searchClear"></a>
						</div>
						<label class="weui-search-bar__label" id="searchText">
							<i class="weui-icon-search"></i>
							<span>搜索</span>
						</label>
					</form>
					<a href="javascript:" class="weui-search-bar__cancel-btn" id="searchCancel">取消</a>
				</div>
				<ul class='list' >
					<li @click="goDetail(tab)" v-for="tab in listArr">
						<p>
							<span class='task_name'>{{tab.requireName}}</span>
							<i class="date">{{tab.startTime.substring(0,10)}}</i>
						</p>
						
						<p>
							<span class='do_user'>操作名称：{{tab.nodeName}}</span>
							<em class='status'>{{tab.status}}</em>
							
						</p>  
						<p>
							<span class='do_user'>发起人：{{tab.crtName}}</span>
						</p>      		
						<p>							
							<em class='code'>{{tab.businessKey}}</em>
							
						</p>       		
						
						
					</li>
				</ul>
				<div class="bottom"  v-if="isLoadMore">
					<span>{{more}}</span>
				</div>
			</div>   
    	</div>
    	 <router-view></router-view>
	</div>
</template>

<script>
	import { getLogin,getTask} from '../../service/service.js'
	let iscroll;
    export default{
    	data(){
    		return{
				isLoadMore:false,
				listArr:[],
				more:"加载中",
				num:0,
				arr:[]
    		}
		},
    	methods:{
    		goDetail(tab){
					// this.$router.push({path:"/to_do/detail",query:{
					// 	info:tab
					// }})
					this.$router.push({path:'/finish'})
    		}
    	},
		mounted(){ 		
			iscroll = new IScroll('#td', {
				probeType: 3
			});
			iscroll.on('scrollStart', ()=>{
			
				if(iscroll.maxScrollY < 0){
					this.isLoadMore = true;
				}
				iscroll.refresh();
			})
			iscroll.on("scroll",()=>{
				if(iscroll.y<=iscroll.maxScrollY-30){
					this.more = "没有数据了"				
				}
			})
		},
		created(){
			var token = localStorage.getItem("token");
			if(token){
				getTask(token).then((res)=>{
					this.listArr = res.tableContent;
					this.num =  res.tableContent.length;
					
				})
			}
			else{
				// console.log("222");
				// this.$router.push("https://open.weixin.qq.com/connect/oauth2/authorize?appid=ww3c1aa17c16e380b7&redirect_uri=https://rfd.roletask.com/Rose&response_type=code&scope=snsapi_base&agentid=AGENTID&state=STATE#wechat_redirect")
				// var url = location.href;
				// var arr = url.split("?");
				// var arr1 = arr[1].split("&")
				// var code = arr1[0].split("=")[1];
				// console.log(code);
				getLogin("NxZV65SSME44GY_XOycz1ue1tv7KZymBi0DbGt8x3Pc").then((result)=>{
					localStorage.setItem("token",result.token);
				})
				var newtoken = localStorage.getItem("token");
				console.log(newtoken);
				
				getTask("af42c1f656bf487cacb602ece0c29dc4MOBILE").then((res)=>{
					this.listArr = res.tableContent;
					this.num =  res.tableContent.length;
					
				})
			}
			//测试代码
			
			// getLogin().then((result)=>{
			// 	console.log(result);
			// 	this.token = result.token;
			// 	localStorage.setItem("token",result.token)
			// 	console.log(this.token);
			// }),
			// getTask().then((res)=>{
			// 	this.listArr = res.tableContent;
			// 	this.num =  res.tableContent.length;
				
			// })
		}
			
    }
</script>

<style>
	#td{
		overflow: hidden;
	}
	.weui-search-bar:before{
		border-top:none;
	}
.list{	
	width:100%;
}
.list li{
	border-bottom:1px solid #e5e5e5;
	padding:10px 15px;
	overflow: hidden;
	color:lightslategrey
}
li p{
	width:100%;
	height: 22px;
	line-height: 22px;
	margin-bottom:4px;
} 
p .task_name{
	display:block;
	width:200px;
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
	font-size:16px;
	color: #666666;	
	margin-bottom: 3px;
	font-weight: 700;
	float:left;
}

p .do_user{
	font-size:14px;
	float:left;
}
p .status{
	/* position: absolute;
	right:40px;
	top:50px; */
	line-height: 22px;
	padding: 0 2px;
	display: inline-block;
	text-align: center;
	background-color:#10AEFF;
	color:#fff;
	float:right;
}
.status.near{
	background-color:#FFBE00 ;
	
}
.status.over{
	background-color:#F76260;
	
}
.code{
	display: inline-block;
}
.date{
	float: right;
	text-align: center;
	color:#666;
}
.bottom{
	width:100%;
	height:30px;
	line-height: 30px;
	text-align: center;
}
</style>




